.page-contain{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 20px;
}

.singleToDo{
    display: flex; /* Use flexbox to align checkbox and text */
    align-items: center; /* Aligns items vertically in the center */
    margin: 10px 0; /* Spacing between list items */
}

.delete-completed{
    flex-direction: column;
    padding: 10px;
}
.wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* Aligns the items to the start of the flex container */
    width: 100%; /* Full width to center content within */
  }
  
.toDoList {
    display: flex; /* Use flexbox to center the list items */
    flex-direction: column; /* Stack list items vertically */
    align-items: center; /* Center list items horizontally */
    width: 100%; /* Full width to center content within */
    padding: 0; /* Remove padding to prevent shift to the right */
  }
  
  ul {
    width: 100%; /* Ensure the ul takes up full width of its parent */
    list-style-type: none; /* Remove bullets from list items */
    padding: 0; /* Reset padding to ensure it doesn't affect centering */
    margin: 0; /* Reset margin to ensure it doesn't affect centering */
  }
  